<!-- "地图"页面 - 百度地图视图组件( 接收经过筛选后的表现数据 ) -->
<template>
    <div id="mapView"></div>
</template>

<script>
import  { mapActions, mapGetters }  from    'vuex'
export default {
    data() {
        return {

        }
    }
    ,mounted: function() {
        this.firstLoading()         // 未来要将切换进来时的 状态清空
    }
    ,methods: {
        // 目的: 状态清空
        firstLoading() {
            // 给 $store的 openMapNum : + 1
            this.initOpenMapNum()
            // 开始判断 '打开次数'
            let openMapNum = this.$store.state.map.mapState.openMapNum.length
            console.log( openMapNum )
            switch( true ) {
                case openMapNum < 2:
                    console.log(' 初次加载 ')
                    break
                case openMapNum > 1 && openMapNum < 5:
                    console.log(' 非初次加载 ')
                    break
                default:
                    console.log(' 准备重置 ')
                    break
            }
        }
        // 目的: 向 $store 的 openMapNum次数 加一( 判断切换次数状态, 用于初始化 )
        ,initOpenMapNum() {
            this.$store.dispatch({
                type: 'initOpenMapNum'
            })
        }
    }

    ,computed: mapGetters({

    })
    ,watch: {

    }
}
</script>

<style lang="sass" scoped>
@import '../../sass/main'

</style>



